.micrograph {
  color: aliceblue;
  text-align: center;
  position: absolute;
  width: 250px;
  height: 250px;
  border: 4px solid #282c34;
}

.micrograph img {
  width: 100%;
  height: 100%;
}

.magnification {
  background: #282c34;
  opacity: 0.8;
  position: absolute;
  top: 0px;
  left: 0px;
  margin: 0;
  padding: 5px;
  min-width: 50px;
}

.constituents {
  background: #282c34;
  opacity: 0.8;
  position: absolute;
  bottom: 0px;
  margin: 0px;
  padding: 5px 0px;
  padding-bottom: 10px;
  width: 100%;
}

.invisible {
  display: none;
  transition: 0.3s;
}

#triangle {
  position: absolute;
  bottom: -24px;
  width: 0px; 
  height: 0; 
  border-left: 127px solid transparent;
  border-right: 127px solid transparent;
  border-top: 20px solid #282c34;
}
